<template>
  <div>
    <Chart :cdata="cdata" />
  </div>
</template>

<script>
import Chart from './chart.vue'
export default {
  data () {
    return {
      drawTiming: null,
      cdata: {
        year: null,
        weekCategory: [],
        weekData1: [],
        weekData2: [],
        weekData3: [],
        weekData4: [],
        weekDataTotal: []
      }
    }
  },
  components: {
    Chart,
  },
  mounted () {
    this.drawTimingFn();
  },
  beforeDestroy () {
    clearInterval(this.drawTiming);
  },
  methods: {
    drawTimingFn () {
      this.setData();
      this.drawTiming = setInterval(() => {
        this.setData();
      }, 6000);
    },
    setData () {
      // 清空轮询数据
      this.cdata.weekCategory = [];
      this.cdata.weekData1 = [];
      this.cdata.weekData2 = [];
      this.cdata.weekData3 = [];
      this.cdata.weekData4 = [];
      this.cdata.weekDataTotal = [];

      let dateBase = new Date();
      this.cdata.year = dateBase.getFullYear();
      // 周数据
      for (let i = 0; i < 10; i++) {
        // 日期
        // let date = new Date();
        // this.cdata.weekCategory.unshift([date.getMonth() + 1, date.getDate()-i].join("/"));
        const element = new Date(new Date().getTime() - i*3600*24*1000)
        this.cdata.weekCategory.unshift((element.getMonth()+1)+'-'+element.getDate())     

        // 柱状图数据
        this.cdata.weekData1.unshift((Math.random()*500+ 200).toFixed(0));
        this.cdata.weekData2.unshift((Math.random()*300+ 200).toFixed(0));
        this.cdata.weekData3.unshift((Math.random()*200+ 100).toFixed(0));
        this.cdata.weekData4.unshift((Math.random()*400+ 100).toFixed(0));
        this.cdata.weekDataTotal.unshift(Number(this.cdata.weekData1[i])+Number(this.cdata.weekData2[i])+Number(this.cdata.weekData3[i])+Number(this.cdata.weekData4[i]));

      }

    }
  }
};
</script>

